.shop-item {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.shop-item-left {
    aspect-ratio: 1 / 1;
    width: 45%;
    max-height: 75%;
    display: flex;
    align-items: center;
    justify-content: center;
}

#shop-item-img {
    max-width: 70%;
    height: 80%;
    object-fit: contain;
}
#shop-item-title {
    height: 25%;
    width: 100%;
    color: black;
    font-size: clamp(.9rem, 1.5vw,1.5rem);
    font-family: 'Montserrat';
    letter-spacing: .2px;
    font-weight: 700;
    display: flex;
    align-items: flex-end;
}
.shop-item-right {
    aspect-ratio: 1 / 1;
    width: 45%;
    max-height: 75%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#shop-item-price {
     color:black;
     height: 12%;
     width: 100%;
     display: flex;
     justify-content: flex-start;
     font-family: 'Lato';
     font-size: 1.2rem;
     border-bottom: 1.5px solid black;
}

#shop-item-rating {
    color: black;
    height: 10%;
    width: 100%;
    font-size: 1rem;
    display: flex;
    justify-content: flex-start;
    font-family: 'Montserrat';
}
#shop-item-rating i {
    font-size: .7rem;
    font-weight: 400;
    margin-left: 2%;
}
#shop-item-category {
    height: 4%;
    color: #444444;
    font-size: 1rem;
    width: 100%;
    font-family: 'Montserrat';
    display: flex;
    justify-content: flex-start;
    letter-spacing: .5px;
    margin-top: 2%;
    margin-bottom: 2%;
  
}
#shop-item-category i {
    color: black;
    font-style: normal;
    margin-left: 2%;
}
#shop-item-description {
    color: black ;
    font-size: clamp(.6rem,.8vw,1.5rem);
    width: 100%;
    letter-spacing: .2px;
    font-family: 'Lato';
    display: flex;
    align-items: flex-start;
    margin-top: 2%;

}
#add-to-cart {
    border: none;
    background-color: black;
    color: white;
    height: 7vh;
    width: 15vw;
    border-radius: 25px;
    font-family: 'Montserrat';
    font-size: .8rem;
    margin-top: 5%;
}
@media (max-width: 768px) {
    .shop-item {
        flex-direction: column;
        overflow: hidden;
    }
    .shop-item-left, .shop-item-right {
        max-height: 45%;
        width: 80%;
    }
    #add-to-cart {
        height: 50px;
        width: 120px;
    }
}